body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f4f4f4;
}

header {
  background-color: royalblue;
  color: white;
  padding: 20px;
  text-align: center;
}

main {
  padding: 20px;
}

section {
  margin-bottom: 40px;
}

footer {
  text-align: center;
  padding: 10px;
  background-color: #333;
  color: white;
}

@media (max-width: 768px) {
  header, footer {
    padding: 10px;
  }

  main {
    padding: 10px;
  }
}
